import React, { useEffect, useState } from 'react';
import './Animation.scss';
import logo from "../../img/logo.png";


const Animation: React.FC = () => {
    const [showMainScreen, setShowMainScreen] = useState(false);

    useEffect(() => {
        // Simle edilmi animasyon sresi (rnein, 3000 ms = 3 saniye)
        const animationDuration = 2000;

        // Animasyon bitiminde ana ekran gster
        const timeoutId = setTimeout(() => {
            setShowMainScreen(true);
        }, animationDuration);

        // Komponent unmount olduunda clearTimeout arlmal
        return () => clearTimeout(timeoutId);
    }, []);

    return (
        <div className={`animation-container ${showMainScreen ? 'hide' : ''}`}>
            <div className="logo-animation">
                <img src={logo} alt="DB Transfer"/>
            </div>
        </div>
    );
};

export default Animation;
