import React, {useState} from 'react';
import './User.scss';
import axios from "axios";

interface IProps {
}

const User: React.FC<IProps> = (props) => {

    const [newUsername, setNewUsername] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [isPasswordVisible, setIsPasswordVisible] = useState(false);

    const togglePasswordVisibility = () => {
        setIsPasswordVisible(!isPasswordVisible);
    };

    const handleSave = () => {
        // Yeni kullanc ad ve ifreyi bir nesne iinde paketleyelim
        const newUser = {
            username: newUsername,
            password: newPassword,
        };

        // Axios ile POST istei gnderelim
        axios.put('https://db-be-d85969a6a61b.herokuapp.com/api/authorization', newUser)
            .then(response => {
            })
            .catch(error => {
            });
    };

    return (
        <div className="user-container">
            <label>
                Yeni Kullanc Ad:
                <input
                    type="text"
                    value={newUsername}
                    onChange={(e) => setNewUsername(e.target.value)}
                />
            </label>
            <br/>
            <label>
                Yeni ifre:
                <input
                    type={isPasswordVisible ? "text" : "password"}
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                />
            </label>
            <br/>
            <div className={'user-container__buttons'}>
                <button className={'user-container__buttons-password'} type="button" onClick={togglePasswordVisibility}>
                    {isPasswordVisible ? "ifreyi Gizle" : "ifreyi Gster"}
                </button>
                <button onClick={handleSave}>Kaydet</button>
            </div>
        </div>
    );
};

export default User;